<template>
  <div class="code">
    <header class="count_top header">
      <img src="../../assets/shopImage/left_icon.png" alt="" @click="back">
      <span>分享二维码</span>
    </header>
    <div class="code_img">
      <img :src="pic" alt="">
    </div>
  </div>
</template>
<script>
    export default {
      data () {
        return {
          pic: '',
          tel:''
        }
      },
      methods:{
        back(){
          this.$router.go(-1);
        }
      },
      mounted(){
        this.post('home/User/getQr',{

        }).then(res=>{
          if (res.data.code === 10000){
            this.pic =   this.imageURL('/'+res.data.info);
            // this.tel = res.data.tel;
          }
        })
      }
    }
</script>
<style scoped>
@import "../../../static/header.css";
  .code {
    height: 100vh;
    background-color: #f2f2f2;
  }
  .code_img {
    width: 100%;
    height: calc(100vh - 0.88rem);
    position: relative;
    overflow: hidden;
  }
  .code_img img{
    width: 100%;
    max-height: 100%;
    position: absolute;
    top: 0;
  }
</style>



